<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card class="box-card">
      <!-- el-row是一个栅格布局，一行有24个栅格，gutter是间距，里面为搜索框和新增按钮 -->
      <el-row :gutter="20">
        <el-col :span="7">
          <!-- clearable开启时，如果点击清空，可以调用clear事件 -->
          <el-input
            placeholder="请输入内容"
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>

      <!-- 用户列表区域，采用了el-table,label是标题，:data绑定要展示的数据 prop是要传递的值，比如姓名对应username，stripe是是否开启索引列，配合type="index"使用 -->
      <el-table :data="userlist" style="width: 100%" border :stripe="true">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label="姓名"> </el-table-column>
        <el-table-column prop="email" label="邮箱"> </el-table-column>
        <el-table-column prop="mobile" label="电话"> </el-table-column>
        <el-table-column prop="role_name" label="角色"> </el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <!-- scope.row对应当前行数据对象，注意作用域插槽会覆盖 prop="xxx" -->
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChange(scope.row)"
              active-color="#409EFF"
              inactive-color="#ff4949"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!-- 删除 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserById(scope.row.id)"
            ></el-button>
            <!-- 分配角色 -->
            <el-tooltip
              effect="dark"
              content="分配角色"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->

      <!-- size-change：显示条目数量改变，则触发函数，current-change：页码切换则触发函数 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 添加用户的对话框，::visible.sync绑定对话框是否显示， -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
    >
      <!-- 内容主体区域 -->

      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改用户的对话框 -->
    <el-dialog
      title="修改用户信息"
      width="50%"
      :visible.sync="editDialogVisible"
      @close="editDialogClose"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 这是检查新增用户邮箱和手机号是否合法的自定义规则，自定义规则需要使用{ validator: checkMobile, trigger: "blur" }来检查
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (regEmail.test(value)) return callback();
      return callback(new Error("输入的邮箱不合法"));
    };
    var checkMobile = (rule, value, callback) => {
      const regMobile =
        /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (regMobile.test(value)) return callback();
      return callback(new Error("输入的手机号不合法"));
    };

    // 以下时常规的data
    return {
      // 获取用户列表的参数对象
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 2,
      },
      userlist: [],
      total: 0,
      // 控制dialog对话框的显示和隐藏
      addDialogVisible: false,
      // 添加用户的表单数据
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      addFormRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "长度在 6 到 15 个字符",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输入电话", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
      // 编辑人员信息窗口是否显示布尔值
      editDialogVisible: false,
      // 编辑窗口查询到的信息
      editForm: {},
      // 编辑区域的校验规则
      editFormRules: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输入电话", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    getUserList() {
      this.$http.get("users", { params: this.queryInfo }).then((res) => {
        if (res.data.meta.status !== 200)
          return this.$message.error("获取用户列表失败");
        this.userlist = res.data.data.users;
        this.total = res.data.data.total;
      });
    },
    // 监听选了一页显示多少条目
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      // 修改要展示的条目数量后要请求并展示新的列表
      this.getUserList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      // 设置新的页面后要请求并展示新的列表
      this.getUserList();
    },
    // 监听switch状态的改变，并传入新值，并修改服务器端的状态
    userStateChange(userinfo) {
      // 根据接口文档，采用put来修改用户状态
      this.$http
        .put("users/" + userinfo.id + "/state/" + userinfo.mg_state)
        .then((res) => {
          if (res.data.meta.status !== 200) {
            // 既然修改失败了，应该把页面上的switch也修改回去
            userinfo.mg_state = !userinfo.mg_state;
            return this.$message.error("状态修改失败");
          }
          return this.$message.success(res.data.meta.msg);
        });
    },
    // 输入query后点击按钮搜索
    search() {
      // 这是一个bug，如果不设置为1，当当前页不在1时，第一页数据会搜索不到
      this.queryInfo.pagenum = 1;
      this.getUserList();
    },
    // 在添加用户的对话框关闭后(@close事件)触发 addDialogClose，重置addForm
    addDialogClose() {
      this.$refs.addFormRef.resetFields();
    },
    // 点击确认增加新用户
    addUser() {
      // 首先校验表单
      this.$refs.addFormRef.validate((valid) => {
        // console.log(valid);
        // 如果校验失败，
        if (!valid) {
          this.$message.error("请正确填写信息后再进行提交");
        }
        // 如果校验成功，则向服务器提交并添加新成员，并关闭对话框
        else {
          this.$http.post("users", this.addForm).then((res) => {
            if (res.data.meta.status !== 201)
              return this.$message.error("添加失败");
            // 如果成功后弹出成功消息，并关闭对话框，再重新刷新列表
            this.addDialogVisible = false;
            this.getUserList();
            return this.$message.success(res.data.meta.msg);
          });
        }
      });
    },

    // 展示编辑用户的对话框，请求并展示已有用户的信息
    showEditDialog(id) {
      this.$http.get("users/" + id).then((res) => {
        if (res.data.meta.status !== 200) {
          return this.$message.error("数据获取失败");
        }
        this.editForm = res.data.data;
      });
      this.editDialogVisible = true;
    },
    // 修改用户信息对话框响应函数
    editDialogClose() {
      this.$refs.editFormRef.resetFields();
    },
    // 修改用户信息确认按钮，发送请求
    editUserInfo() {
      this.$refs.editFormRef.validate((valid) => {
        // console.log(valid);
        if (!valid) {
          return this.$message.error("请输入正确的信息");
        } else {
          this.$http
            .put("users/" + this.editForm.id, {
              email: this.editForm.email,
              mobile: this.editForm.mobile,
            })
            .then((res) => {
              console.log(res);
              if (res.data.meta.status !== 200)
                return this.$message.error("更新失败");
              this.editDialogVisible = false;
              this.getUserList();
              return this.$message.success(res.data.meta.msg);
            });
        }
      });
    },
    // 根据id删除对应的用户信息
    async removeUserById(id) {
      const confirmResult = await this.$confirm(
        "此操作将永久删除该用户, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((error) => error);

      // 点击取消，提示已取消删除
      if (confirmResult !== "confirm") return this.$message.info("已取消删除");
      // 解构 返回值，从其中的属性中将data赋值给res
      const { data: res } = await this.$http.delete("users/" + id);
      // 如果不为200，提升删除失败
      if (res.meta.status !== 200) return this.$message.error("删除失败");
      // 否则刷新列表并提示删除成功
      this.getUserList();
      return this.$message.success(res.meta.msg);
    },
  },
};
</script>

<style lang="less" scoped>
</style>